import {
    Container,
    SimpleGrid,
    Image,
    Flex,
    Heading,
    Text,
    Stack,
    StackDivider,
    Icon,
    useColorModeValue,
  } from '@chakra-ui/react';
  import {
    IoPerson,
    IoLogoBitcoin,
    IoSearchSharp,
    IoWifiSharp,
  } from 'react-icons/io5';
  import logo_perfil from './img/perfil.jpg'
  import { ReactElement } from 'react';
  import Fade from 'react-reveal/Fade'
  interface FeatureProps {
    text: string;
    iconBg: string;
    icon?: ReactElement;
  }
 
  
  const Feature = ({ text, icon, iconBg }: FeatureProps) => {
    
    return (
      <Stack direction={'row'} align={'center'}>
        <Flex
          w={8}
          h={8}
          align={'center'}
          justify={'center'}
          rounded={'full'}
          bg={iconBg}>
          {icon}
        </Flex>
        <Text fontWeight={600}>{text}</Text>
      </Stack>
    );
  };
  
  export default function SplitWithImage() {
    return (
      <Fade right >
      <Container maxW={'5xl'} py={12} id="xp">
        <SimpleGrid columns={{ base: 1, md: 2 }} spacing={10}>
        <Flex>
            <Image
              rounded={'md'}
              alt={'feature image'}
              src={logo_perfil}
              objectFit={'cover'}
            />
          </Flex>
          <Stack spacing={4}>
            <Text
              textTransform={'uppercase'}
              color={'blue.400'}
              fontWeight={600}
              fontSize={'sm'}
              bg={useColorModeValue('blue.50', 'blue.900')}
              p={2}
              alignSelf={'flex-start'}
              rounded={'md'}>
              Experiences
            </Text>
            <Heading>Minhas Experiências :)</Heading>
            <Text color={'gray.500'} fontSize={'lg'}>
            Esses são os locais em que tive o prazer de colaborar e também aprender bastante.
            </Text>
            <Stack
              spacing={4}
              divider={
                <StackDivider
                  borderColor={useColorModeValue('gray.100', 'gray.700')}
                />
              }>
              <Feature
                icon={
                  <Icon as={IoWifiSharp} color={'yellow.500'} w={5} h={5} />
                }
                iconBg={useColorModeValue('yellow.100', 'yellow.900')}
                text={'Subcoordenador de redes - Governo RN '}
              />
              <Text>Gestão de rede e serviços , responsavél técnico e sysadmin  </Text>
              <Feature
                icon={<Icon as={IoPerson} color={'green.500'} w={5} h={5} />}
                iconBg={useColorModeValue('green.100', 'green.900')}
                text={'Estagiario SEMURB - PREFEITURA DO NATAL RN'}
              />
               <Text>Trabalhei com a
linguagem PHP e Mysql desenvolvendo micro sistemas de uso interno</Text>
              <Feature
                icon={
                  <Icon as={IoPerson} color={'purple.500'} w={5} h={5} />
                }
                iconBg={useColorModeValue('purple.100', 'purple.900')}
                text={'Estagiario SMS - PREFEITURA DO NATAL RN'}
              />
              <Text>Estagio na área de manutenção de micro ,experiencia em atendimento ao
usuário , configuração e montagem de redes de pequeno e médio porte .</Text>
            </Stack>
          </Stack>
          
        </SimpleGrid>
      </Container>
      </Fade>
    );
  }